<template>
  <div id="addRoom">
    <head-top title="Add Devices" goBack="true"></head-top>
    <div class="content">
      <div class="list">
        <span>Select the device you want to add</span>
        <ul v-show="listData.length > 0">
          <li v-for="item in listData" :key="item.id">
            <div class="left">
              <img :src="item.icon" alt="">
              <span>{{item.name}}</span>
            </div>
            <div class="right" @click="tabswitch(item)">
              <img src="../../assets/addevice/click@3x.png" alt="" v-if="item.status">
              <img src="../../assets/addevice/defunct@3x.png" alt="" v-if="!item.status">
            </div>
          </li>
        </ul>
        <div class="noList" v-if="listData.length === 0">
          No devices
        </div>
      </div>
      <!-- :class="nameStr === ''? 'disabClick' : ''" -->
      <div class="nextbtn" @click="Finished" >
        <span>Finished</span>
      </div>
    </div>
  </div>
</template>
<script>
import headTop from '../../components/common/head'
import { getStore, setStore } from '../../utils'
// import {
//   addRoomRequest,
//   unbundlingDeviceRequest
// } from '../../api/method'
import {
  addRoomResponse
} from '../../api/getData'
import { mapActions, mapGetters } from 'vuex'
// const heartIMG = require('../../assets/device/heater@3x.png')
// const humideyIMG = require('../../assets/device/humidier@3x.png')
// const lightIMG = require('../../assets/device/light@3x.png')
export default {
  data () {
    return {
      borderColor: false, // input 获取焦点时border颜色
      nameStr: '', // 输入内容
      toastFalg: false, // 输入格式错误提示
      fullHeight: null, // 当前屏幕高度
      listData: null // 设备列表
    }
  },
  components: {
    headTop
  },
  computed: {
    ...mapGetters(['getDevicelist', 'getDevicesdata', 'roomName']),
    deviceList () {
      return this.getDevicelist
    },
    devicesData () {
      return this.getDevicesdata
    },
    RoomName () {
      return this.roomName
    }
  },
  created () {
    this.data_init()
  },
  mounted () {
    // 添加房间返回
    addRoomResponse(this.NAVIGATOR, this)
    this.fullHeight = document.documentElement.clientHeight
    // this.listData = [
    //   {
    //     icon: heartIMG,
    //     type: 'Heater',
    //     status: true
    //   }
    // ]
    console.log(this.listData)
  },
  methods: {
    ...mapActions(['setRoomdata', 'setDevicelist']),
    data_init () {
      this.listData = this.deviceList
      this.listData.forEach(element => {
        element.status = false
      })
    },
    tabswitch (item) {
      console.log(item.status)
      let flag = item.status
      // 视图不会刷新解决办法
      this.$delete(item, 'status')
      this.$set(item, 'status', !flag)
    },
    Finished () {
      // if (this.toastFalg || this.nameStr === '') {
      //   return false // 房间名格式错误
      // }
      let macs = ''
      let icons = []
      let roomData = JSON.parse(getStore('roomData'))
      let flag = true // 未选择设备
      console.log(macs, '添加设备的mac')
      let macArray
      roomData.forEach(element => {
        if (element.name === this.RoomName) {
          icons = element.icon
        }
      })
      if (this.listData.length > 0) {
        macs = this.listData.map(element => {
          if (element.status) {
            flag = false
            icons.push(element.mode)
            return element.mac
          }
        }).filter(item => {
          return item !== undefined // 过滤mac数组中的undefined
        })
      }
      if (flag) {
        this.$router.go(-1)
        return false
      }
      roomData.forEach(element => {
        if (element.name === this.RoomName) {
          element.mac = element.mac ? element.mac + '&' + macs.join('&') : macs.join('&')
          // element.icon = element.icon ? element.icon + '&' + icons.join('&') : icons.join('&')
          macArray = element.mac
        }
      })
      console.log(macArray, '房间mac')
      setStore('roomData', roomData)
      this.setRoomdata(roomData)
      let newdeviceList = this.deviceList
      if (newdeviceList.length) {
        newdeviceList = newdeviceList.filter(item => {
          if (macArray.indexOf(item.mac) < 0) {
            return item
          }
        })
      }
      this.setDevicelist(newdeviceList)
      this.$router.go(-1)
    }
  }
}
</script>
<style lang="less" scoped>
#addRoom{
  .wh(100%, 100%);
  .fs;
  z-index: 999;
  background: white;
  display: flex;
  flex-direction: column;
  .editorName {
    padding: 0 30px;
    .wh-l(100%, 100px);
    border-top: 1px solid #cccccc;
    border-bottom: 1px solid #cccccc;
    display: flex;
    justify-content: space-between;
    input {
      flex: 1;
      font-size: 24px;
      background-color: transparent;
      .wh-l(auto, 100%);
      color: black;
      font-weight: 600px;
    }
    input::-webkit-input-placeholder {
      /* WebKit browsers */
      color: #cccccc;
    }
    img {
      .wh(24px, 24px);
      align-self: center;
    }
  }
  .content{
    display: flex;
    flex-direction: column;
    span{
      padding: 0 30px;
      font-size: 25px;
      line-height: 40px;
      text-align: left;
    }
    span:nth-child(1) {
      font-size: 32px;
      line-height: 92px;
      text-align: left;
      .wh(100%, 92px);
    }
    span:nth-of-type(2){
      margin-top: 20px;
      color: #dadada;
    }
    span:nth-of-type(3){
      color: #dadada;
      // color: #fd403e;

    }
    .list{
      display: flex;
      flex-direction: column;
      span{
        line-height: 110px;
        height: 110px;
      }
      ul{
        li{
          display: flex;
          justify-content: space-between;
          // justify-content: space-between;
          .wh(100%, 93px);
          border-top: 1px solid #dadada;
          padding-left: 30px;
          line-height: 93px;
          .left{
            img{
              .wh(23px,38px);
              margin-right: 30px;
              align-self: center;
            }
            display: flex;
            span{
              font-size: 30px;
              color: #575757;
              line-height: 93px;
            }
          }
          .right{
            display: flex;
            width: 10%;
            justify-content: flex-end;
            padding-right: 30px;
            img{
              .wh(40px,40px);
              align-self: center;
              margin-left: 30px;
            }
          }
        }
        li:last-child {
          border-bottom: 1px solid #dadada;
        }
      }
      .noList{
        border-bottom: 1px solid #dadada;
        border-top: 1px solid #dadada;
        .wh-l(100%, 300px);
        font-size: 25px;
      }
    }
  }
  .active_color {
    border-color: #01a29a;
  }
  .nextbtn{
    .wh-l(520px,100px);
    margin: 0 auto;
    border-radius: 10px;
    background: #13af68;
    margin-top: 120px;
    span{
      font-size: 47px;
      color: #fff;
      vertical-align: middle;
    }
  }
  .disabClick {
    background: #D6D6D6;
    opacity: 0.7;
  }
}
</style>



